/* theme */
:root {
  /* light */
  /* color */
  --bg-dark-opacity: #00000080;
  --bg-default: #ffffff;
  --bg-subtle: #f9fafb;
  --bg-gray: #F9FAFB;
  --border-default: #e0e0e0;
  --surface-default: #f2f2f2;
  --surface-raised: #ececec;
  --text-disabled: #a1a1a1;
  --text-primary: #111111;
  --text-secondary: #626262;
  --text-opposite: #111111;
  /* Value */
  /* color */
  --black: #000000;
  --blue: #2175ff;
  --gray-dark: #1d1d1d;
  --gray-light: #cccccc;
  --green: #37ce00;
  --orange-100: #ff7a00;
  --orange-100-60: #ff7a0099;
  --orange-200: #e55204;
  --red: #ff0000;
  --white: #ffffff;

  
  /* spacing */
  /* Mode 1 */
  /* number */
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 0.75rem;
  --spacing-lg: 1rem;
  --spacing-xl: 1.5rem;

  /* border radius */
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-pill: 999px;

  /* heights */
  --height-md: 40px;
}
$mobile: 480px;
$pc: 1210px;
$pcContainerSub: 700px;

$bgDark: #1D1D1D;
$gnbHeight: 80px;
$mobilePadSide: 15px;
