@use 'variables' as *;

.issue-timeline {
  margin-bottom: 24px;
}

.stock-timeline-card {
  display: flex;
  padding: 16px;
  flex-direction: column;
  gap: 15.998px;
  border-radius: 14px;
  border: 1px solid var(--border-default, #464646);
  background: var(--bg-subtle, #2F2F2F);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.10), 0 1px 2px -1px rgba(0, 0, 0, 0.10);
}

.stock-timeline-header {
  display: flex;
  height: 44.004px;
  justify-content: space-between;
  align-items: flex-start;

  .company-name {
    color: var(--text-primary, #111111);
    font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 140%;
  }

  .price-info {
    display: flex;
    width: 96.934px;
    padding-top: 1.078px;
    flex-direction: column;
    gap: 3.217px;

    .current-price {
      color: var(--red, #FF0000);
      text-align: right;
      font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
      font-size: 20px;
      font-weight: 700;
      line-height: 140%;
    }

    .price-change {
      color: var(--red, #FF0000);
      text-align: right;
      font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
      font-size: 12px;
      font-weight: 500;
      line-height: 150%;
    }
  }
}

.timeline-chart-wrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;

  .chart-card {
    padding: 12px;
  }

  .timeline-chart {
    height: 251px;
    max-height: 251px;
    width: 100%;
    display: block;
  }
}

.timeline-legend {
  display: flex;
  height: 15.005px;
  justify-content: space-between;
  align-items: center;

  .legend-item {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    flex: 1 0 0;
  }

  .legend-marker {
    width: 7.999px;
    height: 7.999px;
    border-radius: 999px;
    background: var(--text-primary, #F0F0F0);

    &--positive {
      border: 1.078px solid var(--red, #FF0000);
    }

    &--negative {
      border: 1.078px solid var(--blue, #2175FF);
    }

    &--neutral {
      border: none;
    }
  }

  .legend-label {
    color: var(--text-primary, #111111);
    font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
    font-size: 12px;
    font-weight: 500;
    line-height: 150%;
  }
}
.chart-tooltip{
  width: 130px;
  padding: 8px;
  border-radius: var(--radius-md, 8px);
  border: 1px solid var(--border-default, #464646);
  opacity: 0.95;
  background: var(--bg-default, #1D1D1D);

  &__date{
    color: var(--text-secondary, #7B7B7B);
    /* Caption/SM */
    font-size: 12px;
    font-weight: 500;
    line-height: 150%; /* 18px */
  }
  &__value{
    color: #FFF;

    /* Body/SM */
    font-size: 15px;
    font-weight: 400;
    line-height: 150%; /* 22.5px */
  }
  &__note{
    margin-top: 4px;
    color: var(--text-primary, #111111);

    /* Caption/SM */
    font-size: 12px;
    font-weight: 500;
    line-height: 150%; /* 18px */
    border-radius: var(--radius-md, 8px);
    border: 1px solid var(--red, #F00);
    background: var(--bg-default, #1D1D1D);
    padding: 12px 8px;
  }
}
